.collection-item {
    width: 22%;
    display: flex;
    flex-direction: column;
    height: 42em;
    align-items: center;
    position: relative;
  
    .image {
      width: 100%;
      height: 37em;
      background-size: cover;
      background-position: center;
      margin-bottom: 5px;
    }
    .custom-button{
       width: 80%;
        opacity: 0.7;
        position: absolute;
        top :550px
        
    }
    &:hover{
      .image{
        opacity: 0.8;
      }
      .custom-button{
        opacity: 0.85;
        display: flex;
      }
    }
  
    .collection-footer {
      width: 100%;
      height: 5%;
      display: flex;
      justify-content: space-between;
      font-size: 18px;
  
      .name {
        width: 90%;
        margin-bottom: 15px;
      }
  
      .price {
        width: 10%;
      }
    }
  }
  